.display{
    flex: 1;
    background-color: var(--bg-color-1);
    padding: 20px;
    overflow-y: scroll;
}

.display p{
    font-size: 1rem;
    line-height: 1.875rem;
    color: var(--text-color-1);
    display: flex;
    justify-content: flex-end;
}

.display_expression {
    overflow-x: scroll;
    width: 100%;
    min-height: 30px;
}

.display_expression p {
    white-space: nowrap;
    min-width: 100%;
    width: fit-content;
    font-weight: bold;
}

.display_result{
    font-weight: bold;
    font-size: 1.3rem !important;
    min-height: 30px;
    
}